/* Reset defaults */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Base styles */
body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  background-color: #f3f4f6;
  min-height: 100vh;
  color: black;
}

/* Layout */
.min-h-screen {
  min-height: 100vh;
  padding: 3rem 1rem;
}

.max-w-3xl {
  max-width: 48rem;
  margin: 0 auto;
}

.bg-white {
  background: transparent;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem;
  padding: 1.5rem;
  color: black;
}

/* Options styling */
.option {
  color: black;
  background: transparent;
  border: 1px solid black;
  transition: all 0.2s ease;
  width: 100%;
  text-align: left;
  padding: 1rem;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
}

.option:hover:not(:disabled) {
  background-color: wheat;
}

.option.correct {
  background-color: #ecfdf5;
  border-color: #34d399;
}

.option.incorrect {
  background-color: #fef2f2;
  border-color: #f87171;
}

/* Button styles */
button {
  cursor: pointer;
  border: none;
  outline: none;
}

button:disabled {
  cursor: default;
  opacity: 0.7;
}

/* Typography */
.text-center {
  text-align: center;
}

.text-2xl {
  font-size: 1.5rem;
  line-height: 2rem;
}

.text-xl {
  font-size: 1.25rem;
  line-height: 1.75rem;
}

.text-sm {
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.font-bold {
  font-weight: 700;
}

.font-semibold {
  font-weight: 600;
}

/* Spacing */
.mb-4 {
  margin-bottom: 1rem;
}

.mb-6 {
  margin-bottom: 1.5rem;
}

.mt-2 {
  margin-top: 0.5rem;
}

.mt-4 {
  margin-top: 1rem;
}

.mt-6 {
  margin-top: 1.5rem;
}

.mr-2 {
  margin-right: 0.5rem;
}

.space-y-4>*+* {
  margin-top: 1rem;
}

.space-x-8>*+* {
  margin-left: 2rem;
}

/* Flexbox */
.flex {
  display: flex;
}

.items-center {
  align-items: center;
}

.justify-between {
  justify-content: space-between;
}

.justify-center {
  justify-content: center;
}

/* Colors */
.text-gray-600 {
  color: black;
}

.text-green-500,
.text-green-600 {
  color: green;
}

.text-red-500,
.text-red-600 {
  color: red;
}

.text-white {
  color: white;
}

.bg-gray-100 {
    background-image: url('https://th.bing.com/th?id=OIP.w4TJoRjZcckn8EplSJX4fAHaE8&w=306&h=204&c=8&rs=1&qlt=90&o=6&dpr=1.1&pid=3.1&rm=2');
      /* Replace with your image URL */
      background-size: cover;
      /* Ensures the image covers the entire background */
      background-repeat: no-repeat;
      /* Prevents the image from repeating */
      background-position: center center;
      /* Centers the image */
      background-attachment: fixed;
      /* Keeps the background image fixed during scroll */
      -webkit-background-size: cover;
      /* Ensures compatibility with WebKit browsers */
      -moz-background-size: cover;
      /* Ensures compatibility with Mozilla browsers */
      -o-background-size: cover;
      /* Ensures compatibility with Opera browsers */
      background-size: cover;
      /* Ensures compatibility with modern browsers */
      image-rendering: auto;
      /* Use browserâ€™s default algorithm for rendering */
      -ms-interpolation-mode: nearest-neighbor;
      /* Enhances image quality in Internet Explorer */
      image-rendering: high-quality;
      /* Suggests the browser to render at high quality */
}

/* Dimensions */
.w-5 {
  width: 1.25rem;
}

.h-5 {
  height: 1.25rem;
}

.w-full {
  width: 100%;
}

/* Button specific styles */
.bg-blue-500 {
  background-color: #3b82f6;
}

.hover\:bg-blue-600:hover {
  background-color: #2563eb;
}

.hover\:bg-gray-100:hover {
  background-color: #f3f4f6;
}

.px-6 {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.py-2 {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.p-4 {
  padding: 1rem;
}

.p-6 {
  padding: 1.5rem;
}

.rounded-lg {
  border-radius: 0.5rem;
}

/* Shadow */
.shadow-lg {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

/* Transitions and Animations */
.transition-colors {
  transition-property: background-color, border-color, color;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.transition-all {
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

/* Cursor */
.cursor-default {
  cursor: default;
}

/* Media Queries */
@media (min-width: 768px) {
  .md\:p-8 {
    padding: 2rem;
  }
}

@media (min-width: 640px) {
  .sm\:px-6 {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}

@media (min-width: 1024px) {
  .lg\:px-8 {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}